
.App {
  text-align: left;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background: #123;
}

.App h2 {
  margin-top: 0;
}

.App p {
  margin-bottom: 0.5em;
}

.curr-time {
  color: #fff;
  padding: 3vw;
}

.banner {
  color: #FFF;
  font-size: 10vw;
  font-weight: bold;
  position: absolute;
  display: block;
  top: 50%; width: 97vw;
  padding-left: 3vw;
  margin-top: -10vw;
  line-height: 2;
  overflow: visible;
}

.banner.highlight {
  color: #fe3;
  text-decoration: underline;
}

.quotation {
  position: absolute;
  bottom: 3vw; left: 3vw; right: 3vw; 
  color: #fff;
}

.demo {
  position: absolute;
  top: 0; right: 0; left: auto; padding: 25px;
  color: #fff;
  background: #90f;
  font-weight: bold;
  font-size: 20px;
  clear: both;
  user-select: none;
  cursor: pointer;
}

.demo.active {
  left: 0;
}

.demo .close {
  float: right;
}